@import url(https://fonts.googleapis.com/css2?family=Source+Sans+Pro&display=swap);
* {
  box-sizing: border-box;
  font-family: 'Source Sans Pro', sans-serif;
}

.header {
  text-align: center;
}

.logo {
  vertical-align: middle;
}

.navbar-nav {
  vertical-align: middle;
}

h1, h2, p, h3 {
  font-family: 'Source Sans Pro', sans-serif;
  font-style: normal;
  text-align: left;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
}


.justify-content-end button {
  margin-left: 10px!important;
}

.left-block, .right-block {
  display: inline-block;
  width: 50%;
  padding: 50px;
  vertical-align: middle;
  text-align: center;
}

.left-block .bl, .right-block .bl {
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  text-align: center;
  margin: 0 auto;
}

.left-block {
  padding-left: 200px;
  padding-right: 50px;
}

.right-block {
  padding-right: 200px;
  padding-left: 50px;
}

.left-block img, .right-block img {
  width: 100%;
}

.no-padding {
  padding: 40px!important;
}

button {
  margin-right: 10px!important;
}

.navbar-nav, .nav-link, .nav-link button {
  text-align: center;
  vertical-align: middle;
}

.slide {
  margin-top: 80px;
  text-align: center;
}

hr {
  margin: 0!important;
}

.row {
  width: 70%;
  margin: 0 auto!important;
  text-align: center;
}

.col {
  /*  */  
  background: #F3F3F3;
  border-radius: 6px;
  width: 80px!important;
  height: 80px!important;
  margin: 15px;
  padding: 10px;
  text-align: center;
}

.col h3 {
  font-size: 1.5rem;
  font-weight: bolder;
  width: 100%;
  text-align: center;
}

.col p {
  font-size: 1rem;
  width: 100%;
  text-align: center;
}

.miniblock span {
  background: #F3F3F3;
  border-radius: 999px;
  padding: 5px;
  padding-left: 10px;
  padding-right: 10px;
}

.card-bl {
  display: inline-block;
  padding: 30px;
  text-align: center;
  margin: 30px;
  max-width: 350px;
  text-align: left;
  border: 1px solid #DADADA!important;
  border-radius: 8px!important;
}

.card-group {
  text-align: center;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  margin: 0 auto;
}

.full-screen {
  background: #241E38;
  width: 100%;
  color: white;
  margin-top: 40px;
  margin-bottom: 40px;
  text-align: center;
  padding: 40px;
  padding-bottom: 0;
}

.full-screen h1, .full-screen h2, .full-screen p {
  width: 100%;
  text-align: center;
}

.full-screen button {
  margin-top: 20px;
  margin-bottom: 20px;
}

.banner img {
  width: 70%;
}

.yellow {color: #E1AF2C;}
.blue {color: #2164E1;}
.green {color: #30B467; -webkit-text-decoration: #30B467 underline; text-decoration: #30B467 underline;}

.last {
  margin-bottom: 100px;
}

.carousel-control-next, .carousel-control-prev {
  color: black!important;
}

@media (min-width: 1600px) {
  .container, .container-lg, .container-md, .container-sm, .container-xl {
    max-width: 1540px!important;
  }
}

@media screen and (max-width: 768px) {
  .left-block, .right-block {
    display: block;
    width: 100%;
    padding: 15px;
  }
  h1 {
    font-size: 1.8rem!important;
  }
  h2 {
    font-size: 1.5rem!important;
  }
  p {
    font-size: 1.2rem!important;
  }
  .slide {
    margin-top: 40px;
  }
  .no-padding {
    padding: 20px!important;
  }
  .row {
    width: 90%!important;
  }
  .banner img {
    width: 100%;
  }
}
